<script>
import PatientService from '@services/patient'
import _debounce from 'lodash/debounce'

export default {
  props: {
      items: {
          type: Array,
          default: function () {
              return []
          },
      },
    visit: {
      type: Object,
      required: true,
    },
  },

  data () {
    return {
      fields: [
        {
          name: 'created_at',
          title: 'Date',
          titleClass: 'ps-3',
          dataclass: 'ps-3',
          width: '10%',
          callback: this.$options.filters.date_DATE_MONTH_YEAR,
        },

        {
          name: 'icd.description',
          title: 'Description',
          titleClass: 'ps-3',
          dataclass: 'ps-3 text-truncate',
          width: '40%',
        },
        {
          name: 'icd.code',
          title: 'Done',
          titleClass: 'ps-3',
          dataclass: 'ps-3',
          width: '20%',
        },
        {
          name: 'user.name',
          title: 'Requested by',
          titleClass: 'ps-3',
          dataclass: 'ps-3 ps-3 text-truncate',
          width: '20%',
        },
        {
          name: '__slot:actions',
        },
      ],

      moreParams: {
        filter: {
          patient_id: this.visit.patient_id,
        },
      },

      diagnoses: [],

      icd10: [],

      form: new window.Form({
        patient_visit_id: this.visit.id,
        provisional_diagnosis: '',
        icd_id: '',
        remarks: '',
      //     new
          item_id:''
      }),

      viewForm: false,

      patientVisitRecords: this.patientVisits,

    }
  },

  computed: {
      productItems () {
          return this.items.filter((item) => item.department.is_laboratory)
      },
    vuetable () {
      return this.$refs.table.$refs.vuetable
    },
  },

  watch: {
    'moreParams.filter.patient_visit_id': function (newId) {
      if (newId) {
        this.vuetable.refresh()
      }
    },
  },

  methods: {
    createDiagnosis () {
      this.form.post('/diagnosis')
        .then(() => {
          this.$bvModal.hide('request-test-modal')

          this.$toast.success('Diagnosis added')

          this.vuetable.refresh()
        })
        .catch()
    },

    deleteDiagnosis (rowData) {
      this.$toast.question('Are you sure', 'Delete Diagnosis').then(() => {
        PatientService.deleteDiagnosis(rowData.id)
          .then(() => {
            this.$toast.success('The diagnosis has been deleted')

            this.vuetable.refresh()
          })
          .catch(() => {
            this.$toast.error('Sorry! The diagnosis could not be deleted')
          })
      })
    },

    getICD10 () {
      this.$httpClient.get('/icd_codes')
        .then(({ data }) => {
          this.icd10 = data.data
        }).catch()
    },

    onDiseaseSearch (search, loading) {
      if (search) {
        loading(true)
        this.searchDisease(search, loading, this)
      }
    },

    searchDisease: _debounce((search, loading, vm) => {
      vm.$httpClient.get('/icd_codes', {
        params: {
          filter: { search },
        },
      })
        .then(({ data }) => {
          vm.icd10 = data.data
          loading(false)
        })
        .catch(() => {
          loading(false)
        })
    }, 1000),
  },
}
</script>

<template>
  <div>
    <div class="card rounded-3 mt-3">
      <div class="card-header">
        <h2 class="page-title">
          Lab Tests
        </h2>
        <div class="card-actions">
          <button
            v-b-modal:request-test-modal
            :disabled="visit.posted_at"
            class="btn btn-outline-primary"
          >
            Request Test
          </button>
        </div>
      </div>
        {{ items.filter((item)=>item.department.is_pharmacy && item.department.is_laboratory)}}
      <VueTable
        ref="table"
        api-url="/diagnosis"
        :fields="fields"
        :append-params="moreParams"
      >
        <template #actions="props">
          <button class="btn btn-white py-1" @click="deleteDiagnosis(props.rowData)">
            <i class="uil uil-trash-alt  text-danger me-1" />
          </button>
        </template>
      </VueTable>
    </div>

    <b-modal
      id="request-test-modal"
      title="Request Test Form"
      title-class="font-18"
      no-close-on-backdrop
      hide-header-close
      footer-class="d-flex justify-content-end bg-light-alpha border-top"
      content-class="rounded-3 bg-white"
      size="lg"
    >
      <form
        id="diagnosisForm"
        @submit.prevent="createDiagnosis"
        @keydown="(event) => form.errors.clear(event.target.name)"
      >
        <div class="mb-3">
          <label for="icd10_id" class="form-label">Products </label>

            <v-select
                v-model="form.item_id"
                :options="productItems"
                label="name"
                :clearable="false"
                :reduce="(option) => option.id"
                name="item_id"
            >
                <template #selected-option="item">
                    <div>
                        <div class="font-weight-medium">
                            {{ item.name }}
                        </div>
                        <div v-if="invoice.cash_credit === 0" class="text-primary small">
                            KES {{ (item.cash_price ? item.cash_price.charge_value * 0.01 : 0) | toMoneyFormat }}
                        </div>
                        <div v-else class="text-primary small">
                            KES {{ (item.scheme_price ? item.scheme_price.charge_value * 0.01 : 0) | toMoneyFormat }}
                        </div>
                    </div>
                </template>
            </v-select>
          <span
            v-if="form.hasError('icd_id')"
            class="text-danger"
            v-text="form.getError('icd_id')"
          />
        </div>

        <div class="mb-3">
          <label for="" class="form-label"> Remarks <small>(optional)</small></label>
          <textarea
            v-model="form.remarks"
            class="form-control"
            rows="5"
          />

          <span
            v-if="form.hasError('remarks')"
            class="text-danger"
            v-text="form.getError('remarks')"
          />
        </div>
      </form>

      <template #modal-footer="{cancel}">
        <button type="button" class="btn me-auto" :disabled="form.processing" @click.prevent="cancel">
          Dismiss
        </button>
        <button
          type="submit" class="btn btn-primary" :disabled="form.processing"
          @click="createDiagnosis"
        >
          Submit
        </button>
      </template>
    </b-modal>
  </div>
</template>
